import React, { Component } from "react";
import { Form, Input, Button } from "antd";
const FormItem = Form.Item;

class App extends Component {
  state = {
    thanhTien: 100,
    record: this.props.record || []
  };
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };
  componentDidMount = () => {
    this.props.form.setFieldsValue({
      name: this.state.record.name,
      namecustomer: this.state.record.namecustomer,
      sdt: this.state.record.sdt,
      adress: this.state.record.adress,
      type: this.state.record.type,
      baohanh: this.state.record.baohanh,
      description: this.state.record.mota,
      tongTrongLuong: this.state.record.tongtrongluong,
      soLuong: this.state.record.soluong,
      donGia: this.state.record.dongia,
      thanhTien: this.state.record.thanhtien,
      nguoiban: this.state.record.nguoiban,
      timesell: this.state.record.timesell
    });
  };
  handleSelectChange = value => {
    console.log(value);
    this.props.form.setFieldsValue({
      name: `Hi, ${value === "male" ? "man" : "lady"}!`
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { typesubmit } = this.props;
    return (
      <Form onSubmit={this.handleSubmit}>
        <FormItem
          label="Tn khch hng"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("namecustomer", {
            rules: [{ required: true, message: "Please input Tn khch hng!" }]
          })(<Input />)}
        </FormItem>

        <FormItem
          label="S in thoi"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("sdt", {
            rules: [{ required: true, message: "Please input S in thoi!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="a ch"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("adress", {
            rules: [{ required: true, message: "Please input a ch!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="Tn sn phm"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("name", {
            rules: [{ required: true, message: "Please input tn sn phm!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="M sn phm"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("type", {
            rules: [{ required: true, message: "Please input loi sn phm!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="Bo hnh"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("baohanh", {
            rules: [{ required: true, message: "Please input bo hnh!" }]
          })(<Input />)}
        </FormItem>

        <FormItem
          label="M t"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("description", {})(<Input />)}
        </FormItem>

        <FormItem
          label="Tng trng lng"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("tongTrongLuong", {
            rules: [
              { required: true, message: "Please input tng trng lng!" }
            ]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="S lng"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("soLuong", {
            rules: [{ required: true, message: "Please input s lng!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="n gi"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("donGia", {
            rules: [{ required: true, message: "Please input n gi!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="Thnh tin"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("thanhTien", {})(<Input />)}
        </FormItem>
        <FormItem
          label="Ngi bn"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("nguoiban", {})(<Input />)}
        </FormItem>
        <FormItem
          label="Thi gian bn"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("timesell", {})(<Input type="dateTime" />)}
        </FormItem>

        <FormItem wrapperCol={{ span: 12, offset: 5 }}>
          <Button type="primary" htmlType="submit">
            {typesubmit ? typesubmit : "Submit"}
          </Button>
        </FormItem>
      </Form>
    );
  }
}

const BanHangForm = Form.create()(App);

export default BanHangForm;



