import React, { Component } from "react";
import { Form, Input, Button } from "antd";
import { DatePicker } from "antd";

const FormItem = Form.Item;

class App extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };
  handleSelectChange = value => {
    console.log(value);
    this.props.form.setFieldsValue({
      name: `Hi, ${value === "male" ? "man" : "lady"}!`
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit}>
        <FormItem
          label="ID sn phm"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("id", {
            rules: [{ required: true, message: "Please input tn sn phm!" }]
          })(<Input />)}
        </FormItem>
        <FormItem label="Li" labelCol={{ span: 5 }} wrapperCol={{ span: 12 }}>
          {getFieldDecorator("name", {
            rules: [{ required: true, message: "Please input li!" }]
          })(<Input type="textarea" />)}
        </FormItem>
        <FormItem
          label="Loi li"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("type", {
            rules: [{ required: true, message: "Please input loi li!" }]
          })(<Input />)}
        </FormItem>
        <FormItem
          label="Thi gian bo hnh"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("baohanh", {
            rules: [
              { required: true, message: "Please input thi gian bo hnh!" }
            ]
          })(<Input type="number" />)}
        </FormItem>

        <FormItem
          label="Thi gian nhn"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("thoigiannhan", {
            rules: [{ required: true, message: "Please input thi gian nhn!" }]
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              placeholder="Select Time"
            />
          )}
        </FormItem>
        <FormItem
          label="Thi gian tr"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("soLuong", {
            rules: [{ required: true, message: "Please input thi gian tr!" }]
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              placeholder="Select Time"
            />
          )}
        </FormItem>
        <FormItem
          label="n gi"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("donGia", {
            rules: [{ required: true, message: "Please input n gi!" }]
          })(<Input type="number" />)}
        </FormItem>
        <FormItem
          label="Thnh tin"
          labelCol={{ span: 5 }}
          wrapperCol={{ span: 12 }}
        >
          {getFieldDecorator("donGia", {})(<Input type="number" />)}
        </FormItem>

        <FormItem wrapperCol={{ span: 12, offset: 5 }}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </FormItem>
      </Form>
    );
  }
}

const BaoHanhForm = Form.create()(App);

export default BaoHanhForm;



